import React from 'react'
import smoothScroll from 'smoothscroll'

export default class Experimental extends React.Component {
  scrollToTop = () => {
    smoothScroll(document.querySelector('.LifeCanvas'))
  }
  
  render = () => (
    <div className='Experimental container'>
      <div className='row'>
        <div className='col-12'>
          <h1>About</h1>
          <p>Hi  Im Mark, a Creative Technologist with Fjord design &amp; innovation.</p>
          <p>I collaborate with curious minds to solve complex problems, with an expertise in fusing design thinking with creative tech capability.</p>
          <p>Im a maker at heart, building tangible artefacts with code to bring ideas to life and drive innovation. </p>
          <p>This website is an experiment in harnessing cellular automata, specifically <a href="https://en.wikipedia.org/wiki/Conway%27s_Game_of_Life" target="_blank" rel="noopener">Conway's Game of Life</a>, to display typically boring website information in a playful yet weird way.</p>
          <p><a href='#' onClick={this.scrollToTop}>Scroll back up</a>  to draw all over it, fuck it up, and have fun </p>
        </div>
      </div>
    </div>
  )
}


